body {
  background-color: #f6f6ec;
}

a {
  font-weight: 500;
  text-decoration-color: #4c3c59;
}
/* unvisited link */
a:link {
  color: #4c3c59;
}

/* visited link */
a:visited {
  color: #4c3c59;
}

/* mouse over link */
a:hover {
  color: #1f6c90;
}

/* selected link */
a:active {
  color: #343547;
}

.App {
  margin: auto;
  max-width: 42rem;
  padding: 2.625rem 1.3125rem;
  color: #343547;
}

.contact {
  float: right;
}

h1,h2,h3{
  font-kerning: auto;
}

h1 {
  font-size: 2.215rem;
  max-width: 100vw;
}

ul.menuBar {
  padding: 0;
}

.menuEmoji {
  border: 2px solid transparent;
}
.menuItems {
  margin-right: 1em;
  cursor: pointer;
}

.menuItems:hover .menuEmoji {
 background-color: #1f6c90;
 border: 2px solid transparent;
 border-radius: 20px;
}

.Resume {
  line-height: 1.6em;
}

td {
  padding: 0.46em;
}

tr:nth-child(even) {
  background-color: #efefef;
}

tr:hover {
  background-color: #ddd;
}

th {
  padding: 8px;
  width: 100vw;
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #1f6c90;
  color: #eee;
}

@media only screen and (max-width: 700px) {

  table {
    font-size: .8em;
    line-height: .9em;
  }
  td {
    padding: .6em 0em;
  }
  
  th {
    padding: 4px;
    width: 100vw;
    padding-top: 8px;
    padding-bottom: 8px;
    text-align: left;
    background-color: #1f6c90;
    color: #eee;
  }
  .contact {
    float: none;
    margin-top: 0;
  }
  .App {
    margin: auto;
    min-width: 80vw;
    color: #343547;
  }
  .menuBar {
    position: fixed;
    top: 1em;
    background-color: #f2c848;
    border-radius: 5px;
    right: 1em;
    float: right;
  }
  .menuItems {
    margin: 0;
  }
  .menuEmoji {
    font-size: 1.2em;
    margin: 2px;
    padding: 4px;
  }
  .menuItems:hover .menuEmoji {
    box-shadow: 0 4px 0 0 #1f6c90;
    background-color: transparent;
    border-color: transparent;
    border-radius: 2px;
   }
  .menuText {
    display: none;
  }

  .Resume {
    padding: 0;
  }
}
